<template>
  <div class="order">
    <h4 class="title">
      <span @click="back" class="back">＜</span>
      <span>确认订单</span>
    </h4>
    <div class="area">
      <van-icon name="plus" />
      <span>新增收货地址</span>
      <van-icon name="arrow" />
    </div>
    <img src="/static/img/01.png" alt />
    <h4 class="lb">商品列表</h4>
    <div class="orderlist">
      <ul>
        <li v-for="item in $store.getters.order" :key="item.id">
          <img :src="item.pic" alt />
          <div>
            <p>{{ item.name }}</p>
            <p>
              <span v-for="p in item.pro" :key="p.id">{{ p.name }}</span>
            </p>
          </div>
          <div>
            <p>￥{{ item.price }}</p>
            <p>x{{ item.num}}</p>
          </div>
        </li>
      </ul>
    </div>

    <div>
      <p>配送方式</p>
      <p>快递</p>
    </div>
    <div>
      <label for="cont">备注</label>
      <input type="text" placeholder="如需备注请输入" />
    </div>
    <p></p>
    <div>
      <p>商品金额</p>
      <p>￥{{ $store.getters.hj }}</p>
    </div>
    <div id="order_foot">
      <p>合计: ￥{{ $store.getters.hj }}</p>
      <p>提交订单</p>
    </div>
  </div>
</template>

<script>
import qs from "qs";

export default {
  data() {
    return {
      address: []
    };
  },
  methods: {
    back() {
      this.$router.go(-1);
    },
    goAddressList() {
      this.$router.push("/addresslist");
    }
  },
  created() {
    console.log(localStorage.getItem("token"));
    this.http
      .get(
        "https://api.it120.cc/small4/user/shipping-address/default?token=" +
          localStorage.getItem("token")
      )
      .then(msg => {
        console.log(msg);
        if (msg.data.msg == "success") {
          this.address = msg.data.data;
        }
      });
  }
};
</script>

<style lang="less" scoped>
.order {
  width: 5.2rem;
  font-size: 0.16rem;
  .title {
    width: 5.2rem;
    height: 0.5rem;
    line-height: 0.5rem;
    text-align: center;
    border-bottom: 0.01rem solid rgb(238, 239, 240);
    .back {
      float: left;
      font-size: 0.3rem;
      text-indent: 0.2rem;
    }
  }
  .area {
    width: 5.2rem;
    height: 0.5rem;
    display: flex;
    line-height: 0.5rem;
    justify-content: space-around;
    margin-top: 0.1rem;
    .van-icon-plus::before {
      width: 0.5rem;
      height: 0.5rem;
      font-size: 0.3rem;
      background: #3283f9;
      border-radius: 0.05rem;
      color: snow;
      text-align: center;
      line-height: 0.5rem;
    }
    .van-icon-arrow::before {
      font-size: 0.25rem;
      line-height: 0.5rem;
    }
    span {
      font-size: 0.16rem;
      color: rgb(144, 149, 153);
    }
  }
  .lb {
    width: 5.2rem;
    height: 0.5rem;
    line-height: 0.5rem;
    font-size: 0.16rem;
    font-weight: 500;
    text-indent: 0.2rem;
    border-top: 0.08rem solid rgb(243, 244, 245);
    border-bottom: 0.03rem solid rgb(238, 238, 238);
  }
  ul {
    border-bottom: 0.15rem solid #f5f5f5;
    li {
      height: 1.53rem;
      border-bottom: 1px solid #e8e8e8;
      display: flex;
      align-items: center;
      > img {
        width: 1.12rem;
        height: 1.12rem;
        display: block;
        margin-left: 0.2rem;
      }
      > div:nth-of-type(1) {
        margin-left: 0.12rem;
        width: 3.06rem;
        height: 1.12rem;
        > p:nth-of-type(1) {
          font-size: 0.18rem;
          margin-top: 0.1rem;
        }
        > p:nth-of-type(2) {
          font-size: 0.18rem;
          margin-top: 0.55rem;
          color: #999;
        }
        > div:nth-of-type(2) {
          height: 1.34rem;
          > p:nth-of-type(1) {
            font-size: 0.18rem;
            margin-top: 0.1rem;
          }
          > p:nth-of-type(2) {
            font-size: 0.18rem;
            margin-top: 0.6rem;
            margin-left: 0.29rem;
            color: #999;
          }
        }
      }
    }
  }

  > div:nth-of-type(2) {
    // height: 0.6rem;
    padding: 0 0.2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 0.18rem;
    border-bottom: 0.01rem solid #e5e5e5;
  }
  > div:nth-of-type(3) {
    height: 0.59rem;
    padding: 0 0.2rem;
    display: flex;
    align-items: center;
    > label {
      font-size: 0.18rem;
    }
    > input {
      width: 3.1rem;
      height: 0.2rem;
      font-size: 0.18rem;
      color: #333;
      margin-left: 0.76rem;
      border: none;
    }
  }
  > p:nth-of-type(2) {
    width: 0%;
    height: 0.01rem;
    background: #0098e9;
    transition: all 1s;
    margin: 0 auto;
  }
  > div:nth-of-type(4) {
    height: 0.64rem;
    padding: 0 0.2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top: 0.15rem solid #f5f5f5;
    border-bottom: 0.15rem solid #f5f5f5;
    > p:nth-of-type(1) {
      font-size: 0.18rem;
    }
    > p:nth-of-type(2) {
      font-size: 0.18rem;
    }
  }
}
#order_foot {
  width: 100%;
  background: white;
  height: 0.6rem;
  position: fixed;
  bottom: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  > p:nth-of-type(2) {
    width: 1.73rem;
    height: 0.69rem;
    background: #b7282e;
    color: white;
    font-size: 0.22rem;
    text-align: center;
    line-height: 0.69rem;
  }
  > p:nth-of-type(1) {
    font-size: 0.2rem;
    color: #d00000;
    line-height: 0.69rem;
    margin-left: 2rem;
  }
}
</style>


