import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Home from '@/pages/Home.vue';
import Index from '@/pages/Index.vue'
import Class from '@/pages/Class.vue';
import My from '@/pages/My.vue'
import Cart from '@/pages/Cart'
import News from '@/pages/News.vue';
import BarList from '@/pages/BarList.vue';
import Reg from '@/pages/Register.vue';
import Log from '@/pages/Login.vue';
import Categood from '@/pages/Categood.vue';
import Detail from '@/pages/Detail.vue';
import Order from '@/pages/Order.vue';
import Addresslist from '@/pages/Addresslist'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
      redirect: '/home'
    },
    {
      path: '/home',
      name: 'Home',
      component: Home,
      redirect:'/home/index',
      children:[
        {
          path:'index',
          component:Index
        },
        {
          path:'class',
          component:Class
        },
        {
          path:'cart',
          component:Cart
        },
        {
          path:'my',
          component:My
        },
        
      ]
    },
    {
      path:'/news',
      name:"news",
      component:News
    },
    // {
    //   path:'/news1',
    //   component:News
    // },
    {
      path:'/barlist',
      name:"barlist",
      component:BarList
    },
    {
      path:'/reg',
      name:"reg",
      component:Reg  
    },
    {
      path:'/log',
      name:"log",
      component:Log   
    },
    {
      path:'/good',
      name:'good',
      component:Categood
    },
    {
      path:'/detail/:id',
      name:'detail',
      component:Detail
    },
    {
      path:'/order',
      name:'order',
      component:Order
    },
    {
      path:'/addresslist',
      name:'addresslist',
      component:Addresslist
    }
  ]
})



