import Vue from 'vue';
import Vuex from 'vuex';
// 数据持久化
import createPersistedState from 'vuex-persistedstate';
Vue.use(Vuex);

export default new Vuex.Store({
    state:{
        title:'hello',
        cart:[]
    },
    mutations:{
        addCart(state,goods){
            var n = state.cart.findIndex((item) => {
                return item.id == goods.findIndex
            });
            console.log(n)
            if(n == -1){
                state.cart.push(goods)
            }else{
                var cart = state.cart;
                cart[n].num += goods.num
                state.cart = cart
            }
        },
        choice(state,d){
            var cart = state.cart;
            cart[d.index].checked = d.value
            state.cart = cart
        },
        num(state,d){
            var cart = state.cart;
            cart[d.index].num = d.value
            state.cart = cart
        },
        all(state,d){
            var cart = state.cart;
            cart.forEach((item)=> {
                item.checked = d
            })
            state.cart = cart
        },
        delet(state){
            let flag = false
            for(let i=0;i<state.cart.length;flag?i:i++){
                if(state.cart[i].checked == true){
                    state.cart.splice(i,1)
                    flag = true
                }else{
                    flag = false
                }
            }
        }
    },
    action:{

    },
    getters:{
        nums(state){
            var s = 0;
            state.cart.forEach((item) => {
                s += item.num
            })
            return s
        },
        hj(state){
            var s = 0;
            state.cart.forEach((item)=> {
                if(item.checked){
                    s += item.num*item.price
                }
            })
            return s
        },
        select(state){
            var re;
            if(state.cart.length>0){
                re = state.cart.every((item)=> {
                    return item.checked
                })
            }
            return re
        },
        order(state){
            return state.cart.filter((item)=>{
                return item.checked
            })
        }
    },
    plugins:[createPersistedState()]
})




