<template>
  <div>
    <div class="h">
      <div class="title">
        人气推荐
        <span>></span>
      </div>
      <div class="hot2">
        <ul>
          <li v-for="item in hot" :key="item.id" @click="detail(item.id)">
            <img :src="item.pic" alt />
            <h5>{{ item.name }}</h5>
            <p class="des">{{ item.characteristic }}</p>
            <p class="price">￥{{ item.minPrice }}</p>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      hot: [],
      loading: true
    };
  },
  created() {
    this.http.get("https://api.it120.cc/small4/shop/goods/list").then(msg => {
      // console.log(msg.data.data);
      this.hot = msg.data.data;
      // console.log(this.hot)
      this.loading = false;
      // let str = msg.data.data;
      // for(let i in str) {
      //   this.hot = str
      // }
    });
  },
  methods: {
    detail(id) {
      this.$router.push({ name: "detail", params: { id } });
    }
  }
};
</script>

<style scoped>
.h {
  width: 5.2rem;
  font-size: 0.16rem;
}
.title {
  width: 5.2rem;
  height: 0.5rem;
  line-height: 0.5rem;
  font-size: 0.18rem;
  text-align: center;
  border-bottom: 1px solid rgb(216, 216, 216);
}
.hot2 {
  width: 5.2rem;
}
ul {
  width: 5.2rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.hot2 li {
  width: 2.5rem;
  height: 3.58rem;
  display: flex;
  font-size: 0.16rem;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
li img {
  width: 2.2rem;
  height: 2.85rem;
  border-radius: 0.1rem;
}
li h5 {
  font-size: 0.16rem;
  font-weight: 600;
}
li p.price {
  font-size: 0.14rem;
  color: brown;
  width: 2.2rem;
  height: 0.25rem;
}
li p.des {
  color: rgb(124, 137, 155);
  width: 2.2rem;
  height: 0.25rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>


