<template>
  <div>
    <div class="bar">
      <div class="label">
        <span @click="jump">＜</span>
        全名砍价
      </div>
      <div class="barlist" v-for="item in bargain" :key="item.id">
        <div class="left">
          <img :src="item.pic" alt />
        </div>
        <div class="right">
          <p class="title">{{ item.name }}</p>
          <p class="descript">{{ item.characteristic=="" ? "请查看具体详情" : item.characteristic }}</p>
          <ul>
            <li>
              <span class="cheap">￥{{ item.minPrice }}</span>
              <span class="origin">￥{{ item.originalPrice }}</span>
              <span class="num">{{ item.stores }}件</span>
            </li>
            <li>
              <span class="cheap2">低价</span>
              <span class="origin">原价</span>
              <span class="num">限量</span>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      bargain: []
    };
  },
  created() {
    this.http
      .get("https://api.it120.cc/small4/shop/goods/kanjia/list")
      .then(msg => {
        console.log(msg.data.data.goodsMap);
        this.bargain = msg.data.data.goodsMap;
      });
  },
  methods:{
      jump(){
          this.$router.push({path:'/home/index'})
      }
  }
};
</script>

<style scoped>
.bar {
  width: 5.2rem;
  font-size: 0.16rem;
}
.label {
  width: 5.2rem;
  height: 0.5rem;
  font-size: 0.2rem;
  line-height: 0.5rem;
  font-weight: bold;
  text-align: center;
  border-bottom: 1px solid rgb(203, 204, 206);
}
.barlist {
  width: 5.2rem;
  height: 1.6rem;
  display: flex;
  justify-content: space-around;
  border-bottom: 1px solid rgb(223, 223, 223);
}
.barlist .left {
  width: 1.58rem;
  height: 1.58rem;
}
.barlist .left img {
  width: 1.58rem;
  height: 1.58rem;
}
.barlist .right {
  width: 2.5rem;
  height: 1.58rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.descript {
  color: rgb(116, 136, 156);
  font-size: 0.16rem;
}
.cheap {
  font-size: 0.15rem;
  color: red;
}
.origin {
  color: rgb(138, 143, 148);
  font-size: 0.14rem;
}
.num {
  font-size: 0.14rem;
  color: rgb(138, 143, 148);
}
ul li {
  height: 0.3rem;
  line-height: 0.3rem;
}
</style>


