<template>
  <div>
    <div class="log">
      <h4>登录账号</h4>
      <span>虾米-严选商城欢迎您</span>
      <p :class="{'active':isActive}">
        <input @blur="lose" type="tel" placeholder="手机号码" v-model="tel" />
        <span class="tian" v-if="isActive">必填</span>
        <van-icon name="phone-o" />
      </p>
      <p :class="{'focus':isFocus}">
        <input  @blur="lost" v-model="pwd" type="password" placeholder="密码" v-if="hid" />
        <input v-model="pwd" type="text" placeholder="密码" v-else />
        <van-icon @click="show" name="closed-eye" v-if="hid" />
        <van-icon @click="show" name="eye-o" v-else />
      </p>
      <button  @click="login" >登录</button>
      <span>忘记密码</span>
      <p class="reg">
        <a @click="register">没有账号？立即注册</a>
      </p>
    </div>
  </div>
</template>

<script>
import qs from 'qs';

export default {
  data() {
    return {
      hid: true,
      pwd:"",
      tel:"",
      isActive: false, // input框鼠标失去焦点和获取焦点时的样式状态//默认为正常框
      isFocus: false, //input框鼠标失去焦点和获取焦点时的样式状态//默认为正常框
    };
  },
  methods:{
    login(){
      var data = {
        "mobile":this.tel,
        "pwd":this.pwd
      }
      this.http.post("https://api.it120.cc/small4/user/m/login?deviceId=007&deviceName=monkey",qs.stringify(data)).then((msg) => {
        console.log(msg)
        if(msg.data.msg == "success"){
          localStorage.setItem("token",msg.data.data.token)
          this.$router.push("/home/my")
        }else{
          alert('用户名或者密码不正确')
        }
      })
    },
    show() {
      this.hid = !this.hid
    },
    register() {
      this.$router.push({path:'/reg'})
    },
        // 手机号失去焦点
    lose() {
      if (this.tel == "") {
        this.$notify("请输入正确手机号");
        this.isActive = true;
      }
    },
    // 密码失去焦点
    lost() {
      if (this.pwd == "") {
        this.$notify("请输入正确密码");
        this.isFocus = true;
      }
    },
  }
};
</script>

<style scoped>
.log {
  width: 5rem;
  height: 6.5rem;
  border: 1px solid rgb(235, 232, 232);
  margin: 0 auto;
  margin-top: 0.1rem;
  padding-top: 0.2rem;
  font-size: 0.16rem;
}
h4 {
  width: 5.2rem;
  text-align: center;
  margin-bottom: 0.2rem;
  font-size: 0.2rem;
}
p {
  width: 3.8rem;
  height: 0.6rem;
  margin-top: 0.2rem;
  font-size: 0.18rem;
  background: rgb(233, 231, 247);
  margin-left: 0.6rem;
  border-radius: 0.15rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  padding: 0rem 0.3rem 0 0;
}
p input {
  width: 3rem;
  border: cornsilk;
  background: rgba(0, 0, 0, 0);
  text-indent: 0.3rem;
}
input ::placeholder {
  color: #000000;
}
span {
  width: 5.2rem;
  text-align: center;
  display: inline-block;
  font-size: 0.16rem;
  color: #807e7e;
}
button {
  width: 2.91rem;
  height: 0.6rem;
  border-radius: 0.06rem;
  background: linear-gradient(135deg, #e570e7 0%, #79f1fc 100%);
  text-align: center;
  line-height: 0.6rem;
  font-size: 0.2rem;
  color: white;
  letter-spacing: 0.06rem;
  margin-left: 1.1rem;
  margin-top: 0.2rem;
  margin-bottom: 0.1rem;
  border: #bb87f6;
}
.reg {
  background: #ffffff;
  text-align: center;
  text-indent: 0;
}
.reg a {
  width: 3.8rem;
  height: 0.5rem;
  color: #5ea6f8;
  text-decoration: underline;
  text-align: center;
}
.tian{
  color: red;
}
.active {
  border: 0.01rem solid rgb(250, 102, 85);
  background: rgb(252, 139, 126, 0.2);
}
.focus {
  border: 0.01rem solid rgb(250, 102, 85);
  background: rgb(252, 139, 126, 0.2);
}
</style>




