<template>
  <div class="Detail">
    <!-- 商品图片轮播 -->
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item v-for="item of banner" :key="item.id">
        <img :src="item.pic" />
      </van-swipe-item>
    </van-swipe>
    <!-- 商品名字部分 -->
    <div class="part">
      <div class="back" @click="back">
        <p>
          <van-icon name="arrow-left" />
        </p>
      </div>
      <p class="title">{{ des.name }}</p>
      <p class="chara">{{ des.characteristic }}</p>
      <div class="price">
        <p class="min">
          低价{{ des.minPrice }}
          <span>￥</span>
        </p>
        <p class="ori">原价{{ des.originalPrice }}</p>
        <p>库存{{ des.stores }}</p>
      </div>
    </div>
    <!-- 商品介绍和商品评价切换 -->
    <div class="nav">
      <van-tabs @click="onClick">
        <van-tab title="商品介绍" name="a">
          <div class="navdl" v-html="con.content"></div>
        </van-tab>
        <van-tab title="商品评价" name="b">
          <div v-for="item in raise" :key="item.id">
            <ul class="xq">
              <li>
                <span>
                  <van-icon name="user-circle-o" />
                </span>
                <div>
                  <div>
                    <p>{{ item.user.nick == "" ? "匿名用户" : item.user.nick }}</p>
                    <p>{{ item.goods.goodReputationStr }}</p>
                  </div>
                  <p>{{ item.goods.goodReputationRemark }}</p>
                  <div>
                    <p>{{ item.goods.dateAdd }}</p>
                    <p>选择规格：{{ item.goods.property }}</p>
                  </div>
                </div>
              </li>
            </ul>
          </div>
        </van-tab>
      </van-tabs>
    </div>
    <!-- 底部购物车收藏购买功能 -->
    <van-goods-action>
      <van-goods-action-icon icon="service-o" text="客服" color="#000" />
      <van-goods-action-icon @click="cart" icon="cart-o" text="购物车" :badge="$store.getters.nums" />
      <van-goods-action-icon @click="collect" v-if="hid" icon="star-o" text="收藏" color="#000" />
      <van-goods-action-icon @click="collect" v-else icon="star" text="已收藏" color="red" />
      <van-goods-action-button type="warning" @click="addCart" text="加入购物车" />
      <van-goods-action-button type="danger" @click="mai" text="立即购买" />
      <!-- 加入购物车 -->
      <van-action-sheet v-model="show" :style="{ height: '64%' }">
        <!-- 点击加入购物车弹出弹框功能 -->
        <div class="content">
          <div>
            <img :src="des.pic" alt />
            <div>
              <p class="name">{{ des.name }}</p>
              <p class="min">￥{{ des.minPrice }}</p>
            </div>
            <van-icon @click="cross" class="cross" name="cross" />
          </div>
          <div>
            <div v-for="(p,index1) in ps" :key="p.id">
              <p>{{ p.name }}</p>
              <div>
                <div class="redwu"
                  :class="{'active':p2.checked}"
                  @click="select(index1,index2)"
                  v-for="(p2,index2) in p.childsCurGoods"
                  :key="p2.id">{{ p2.name }}</div>
              </div>
            </div>
          </div>
          <div>
            <van-field name="stepper" label="购买数量">
              <template #input>
                <van-stepper v-model="stepper" />
              </template>
            </van-field>
          </div>
          <p  @click="add">加入购物车</p>
        </div>
      </van-action-sheet>
      <!-- 立即购买 -->
      <van-action-sheet v-model="addsp" :style="{ height: '64%' }">
        <div class="content">
          <div>
            <img :src="des.pic" alt />
            <div>
              <p class="name">{{ des.name }}</p>
              <p class="min">￥{{ des.minPrice }}</p>
            </div>
            <van-icon @click="cross" class="cross" name="cross" />
          </div>
          <div>
            <div v-for="(p,index1) in ps" :key="p.id">
              <p>{{ p.name }}</p>
              <div>
                <div
                  class="redwu"
                  :class="{'active':p2.checked}"
                  @click="select(index1,index2)"
                  v-for="(p2,index2) in p.childsCurGoods"
                  :key="p2.id"
                >{{ p2.name }}</div>
              </div>
            </div>
          </div>
          <div>
            <van-field name="stepper" label="购买数量">
              <template #input>
                <van-stepper v-model="stepper" />
              </template>
            </van-field>
          </div>
          <p>立即购买</p>
        </div>
      </van-action-sheet>
    </van-goods-action>
  </div>
</template>

<script>
import { Toast } from "vant";

export default {
  data() {
    return {
      des: [], //商品介绍数据
      banner: [],  //轮播图数据
      con: [], //商品评价数据
      activeName: "a", //控制商品介绍和商品评价切换
      raise: [],
      hid: true, //收藏与已收藏功能
      show: false, // 控制加入购物车弹出层
      addsp: false, // 控制立即购买弹出层
      stepper: 1, //商品数量
      ps: []  //弹出层中选择商品规格数据
    };
  },
  methods: {
    // 返回
    back() {
      this.$router.go(-1);
    },
    // 收藏图标
    collect() {
      this.hid = !this.hid;
    },
    addCart() {
      this.show = true;
    },
    mai() {
      this.addsp = true;
    },
    // 商品介绍和商品评价切换
    onClick(name, title) {
      Toast(title);
    },
    // 隐藏弹出层
    cross() {
      this.show = false;
      this.addsp = false;
    },
    // 商品规格选中效果
    select(n1, n2) {
      this.ps[n1].childsCurGoods.forEach((item, index) => {
        if (index == n2) {
          item.checked = true;
          this.$set(this.ps[n1].childsCurGoods, index, item);
        } else {
          item.checked = false;
          this.$set(this.ps[n1].childsCurGoods, index, item);
        }
      });
    },
    // 点击加入购物车载入数据
    add() {
      var t = localStorage.getItem("token");
      // console.log(t)
      if(t){
        var p0 =[]
        this.ps.forEach((item) =>{
          var r = item.childsCurGoods.filter((p) => {
            return p.checked
          });
          p0 = p0.concat(r)
        })
        console.log(p0)
        if(p0.length == 0){
          this.$notify('请选择属性')
          return false
        }
        var obj = {
          pic:this.con.basicInfo.pic,
          name:this.con.basicInfo.name,
          price:this.con.basicInfo.minPrice,
          num:this.stepper,
          pro:p0,
          checked:false,
          id:this.con.basicInfo.id
        }
        // 提交到state仓库中
        this.$store.commit('addCart',obj)
        Toast.success('成功加入');

      }else{
        // 如果没有登录跳转到登录页面
        this.$router.push('/log')
      }
    },
    // 点击购物车图标跳转到购物车页面
    cart() {
      this.$router.push( {path:'/home/cart' })
    }
  },
  created() {
    // 接收分类商品接口
    var id = this.$route.params.id;
    // 商品介绍接口
    this.http
      .get("https://api.it120.cc/small4/shop/goods/detail?id=" + id)
      .then(msg => {
        // console.log(msg);
        this.banner = msg.data.data.pics;
        this.des = msg.data.data.basicInfo;
        this.con = msg.data.data;
        console.log(this.con);

        var arr = this.con.properties;
        arr.forEach(item => {
          item.childsCurGoods.forEach(a => {
            a.checked = false;
          });
        });
        this.ps = arr;
        console.log(this.ps);
      });
    // 商品评价接口
    this.http
      .post("https://api.it120.cc/small4/shop/goods/reputation?goodsId=" + id)
      .then(msg => {
        // console.log(msg);
        this.raise = msg.data.data;
        // console.log(this.raise);
      });
  }
};
</script>

<style>
* {
  margin: 0;
  padding: 0;
}
.Detail {
  width: 5.2rem;
  font-size: 0.16rem;
}
.Detail .navdl li {
  height: 0.48rem;
  box-sizing: border-box;
  padding-left: 24px;
  margin-top: 0.15rem;
}
.Detail .img-lazyloaded {
  width: 100% !important;
  height: 130px !important;
}
.Detail .my-swipe {
  width: 5.2rem;
  height: 3.4rem;
}

.Detail .my-swipe .van-swipe-item {
  width: 5.2rem;
  height: 3.4rem;
}
.Detail .my-swipe .van-swipe-item img {
  width: 100%;
  height: 3.4rem;
}
.Detail .part {
  width: 5rem;
  height: 1.24rem;
  background: #ffffff;
  padding-left: 0.2rem;
  border-bottom: 0.14rem solid #f5f5f5;
}
.Detail .title {
  font-size: 0.22rem;
  color: #464646;
  margin-top: 0.3rem;
}
.Detail .chara {
  font-size: 0.16rem;
  color: #b8b8b8;
  margin-top: 0.16rem;
}
.Detail .price {
  display: flex;
  margin-top: 0.1rem;
}
.Detail .min {
  font-size: 0.14rem;
  color: #cc0c0c;
  width: 0.9rem;
}
.Detail .min span {
  font-size: 0.2rem;
}
.Detail .ori {
  font-size: 0.14rem;
  color: #b8b8b8;
  margin-top: 0.06rem;
  width: 3.4rem;
}
.Detail .xq {
  width: 100%;
}
.Detail .xq li {
  height: 1.54rem;
  padding: 0 0.2rem;
  border-bottom: 0.01rem solid #e9e9e9;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.Detail van-icon {
  width: 0.8rem;
  height: 0.8rem;
  font-size: 0.3rem;
}
.Detail .van-icon-user-circle-o::before {
  font-size: 0.8rem;
}
.Detail .xq:nth-last-child(1) li > div {
  margin-left: 0.14rem;
}
.Detail .xq:nth-last-child(1) li > div > div:nth-of-type(1) {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  margin-top: 0.18rem;
}
.Detail .xq:nth-last-child(1) li > div > div:nth-of-type(2) {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-top: 0.2rem;
}
.Detail .xq:nth-last-child(1) li > div > div:nth-of-type(1) p:nth-of-type(1) {
  font-size: 0.16rem;
}
.Detail .xq:nth-last-child(1) li > div > div:nth-of-type(1) p:nth-of-type(2) {
  width: 0.48rem;
  height: 0.32rem;
  border-radius: 0.06rem;
  color: white;
  text-align: center;
  line-height: 0.32rem;
  background: #b7282e;
  font-size: 0.16rem;
  margin-left: 0.2rem;
}
.Detail .xq:nth-last-child(1) li > div > div:nth-of-type(2) > p {
  font-size: 0.12rem;
  color: #b2b2b2;
}
.Detail .xq:nth-last-child(1) li > div > p:nth-of-type(1) {
  font-size: 0.16rem;
  margin-top: 0.1rem;
}
.Detail .back {
  width: 0.36rem;
  height: 0.36rem;
  padding: 0.03rem;
  border-radius: 50%;
  border: 0.02rem solid #ffffff;
  position: fixed;
  left: 0.1rem;
  top: 0.1rem;
  text-align: center;
  line-height: 0.36rem;
  z-index: 123;
}
.Detail .back > p {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: white;
  text-align: center;
}
.Detail .back p > i {
  font-size: 0.3rem;
  color: #666;
  margin-top: 0.02rem;
}
.Detail .content > div:nth-of-type(1) {
  height: 1.23rem;
  border-bottom: 0.01rem solid #e9e9e9;
  display: flex;
}
.Detail .content > div:nth-of-type(1) > img {
  width: 0.85rem;
  height: 0.85rem;
  display: block;
  margin-left: 0.2rem;
  margin-top: 0.2rem;
}
.Detail .content > div:nth-of-type(1) > div:nth-of-type(1) {
  margin-left: 0.16rem;
  width: 3.5rem;
}
.Detail .content > div:nth-of-type(1) > div:nth-of-type(1) > p:nth-of-type(1) {
  font-size: 0.17rem;
  margin-top: 0.3rem;
}
.Detail .content > div:nth-of-type(1) > div:nth-of-type(1) > p:nth-of-type(2) {
  font-size: 0.18rem;
  color: #d00000;
  margin-top: 0.24rem;
}
.Detail .content > div:nth-of-type(2) {
  padding: 0.3rem 0;
  border-bottom: 0.01rem solid #e9e9e9;
}
.Detail .content > div:nth-of-type(2) > div > p {
  font-size: 0.16rem;
  margin-left: 0.2rem;
  margin-top: 0.1rem;
}
.Detail .content > div:nth-of-type(2) > div > div {
  display: flex;
  flex-wrap: wrap;
  margin-top: 0.1rem;
}
.Detail .content > div:nth-of-type(2) > div > div > div {
  padding: 0.1rem 0.2rem;
  border-radius: 0.06rem;
  margin-top: 0.1rem;
  margin-left: 0.1rem;
}
.Detail .redwu {
  border: 0.01rem solid #d9d9d9;
}

.Detail .content > p:nth-of-type(1) {
  width: 100%;
  height: 0.6rem;
  text-align: center;
  line-height: 0.6rem;
  font-size: 0.22rem;
  color: white;
  background: #b7282e;
}
.Detail .cross {
  width: 0.5rem;
  height: 0.6rem;
  font-size: 0.3rem;
  line-height: 0.6rem;
}
.Detail .active{
  border: 0.01rem solid salmon;
}
</style>


