<template>
  <div>
    <div class="topics">
      <div class="top">
        精选专题
        <span>＞</span>
      </div>
      <div class="toplist">
        <ul>
          <li v-for="item in topic" :key="item.id" @click="gonews(item.id)">
            <img :src="item.pic" alt />
            <h4>{{ item.title }}</h4>
            <p>{{ item.descript }}</p>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      topic: []
    };
  },
  methods: {
    gonews(id) {
      this.$router.push({ name: "news", query: { id: id } }); 
      // this.$router.push("news?id"+id)  //get传值
      // this.$router.push("news/:id+id") //动态路由
    }
  },
  created() {
    this.http.get("https://api.it120.cc/small4/cms/news/list").then(msg => {
      console.log(msg.data.data);
      this.topic = msg.data.data;
    });
  }
};
</script>

<style scoped>
.topics {
  width: 5.2rem;
  height: 0.5rem;
  line-height: 0.5rem;
  font-size: 0.16rem;
}
.top {
  width: 5.2rem;
  height: 0.5rem;
  font-size: 0.2rem;
  line-height: 0.5rem;
  font-weight: bold;
  text-align: center;
  border-bottom: 1px solid rgb(203, 204, 206);
}
.topics {
  width: 5.2rem;
  height: 3.75rem;
  border-bottom: 0.2rem solid rgb(240, 240, 240);
}
.topics ul {
  width: 17rem;
  height: 3.2rem;
  display: flex;
}
.toplist {
  width: 5.2rem;
  height: 3.2rem;
  overflow: auto;
}
.topics ul li {
  width: 3.2rem;
  height: 3.2rem;
  margin-right: 0.2rem;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.topics ul li img {
  width: 3.2rem;
  height: 2.5rem;
  border-radius: 0.15rem;
}
.topics ul li h4 {
  font-size: 0.16rem;
  width: 2.7rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.topics ul li p {
  width: 2.7rem;
  font-size: 0.16rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;

  font-size: 0.14rem;
  color: slategrey;
}
</style>


