<template>
  <div class="mylog">
    <div class="header">
      <div class="left">
        <img src="http://39.105.231.166:7002/static/toux.jpg" alt />
      </div>
      <div class="right">
        <p @click="login">立即登录</p>
        <p>积分：5</p>
        <p @click="out">退出登录</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
    data() {
        return {
            hid:true,
        }
    },
    methods:{
        login() {
            this.$router.push({ path:'/log' })
        },
        out() {
            localStorage.removeItem("token");
            this.$router.push({ path:'/log' })
        }
    }

};
</script>


<style scoped>
.mylog{
    width: 5.2rem;
    font-size: 0.16rem;
}
.header{
    width: 5.2rem;
    height: 2.15rem;
    background: rgb(194, 165, 112);
    color: #ffffff;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.left{
    width: 1.06rem;
    height: 1.06rem;
}
.left img{
  width: 1.06rem;
  height: 1.06rem;
}
.right{
    width: 3rem;
    height: 1.5rem;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
</style>


