<template>
  <div class="ca">
    <p class="search">
      <input type="text" placeholder="回车搜索商品" />
    </p>
    <div class="cate">
      <ul>
        <li @click="all" :class="{'active': n==0}" class="fen">所有分类</li>
        <li
          class="fen"
          :class="{'active': (index+1==n)}"
          v-for="(item,index) in a1"
          :key="item.id"
          @click="getChildren(item.id,index+1)"
        >{{ item.name }}</li>
      </ul>
      <div>
        <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
          <van-swipe-item v-for="(item,index) in banner" :key="index">
            <img :src="item.img" alt />
          </van-swipe-item>
        </van-swipe>
        <ol>
          <li v-for="item of a2" :key="item.id" @click="des(item.id)">
            <img :src="item.icon" alt />
            <p>{{ item.name }}</p>
          </li>
        </ol>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      a1: [],
      a2: [],
      a3: [],
      n: 0,
      banner: [
        { img: "static/img1/tup1.jpg" },
        { img: "static/img1/tup2.jpg" },
        { img: "static/img1/tup3.jpg" },
        { img: "static/img1/tup4.jpg" }
      ]
    };
  },
  methods: {
    async getData() {
      var d = await this.http.get(
        "https://api.it120.cc/small4/shop/goods/category/all"
      );
      // console.log(d);

      if (d.data.msg == "success") {
        var c1 = d.data.data.filter(item => {
          return item.level == 1;
        });

        this.a1 = c1; //把以及分类放a1里
        this.a3 = d.data.data;
        this.a2 = d.data.data;
      }
    },
    getChildren(id, index) {
      var d2 = this.a3.filter(item => {
        return item.pid == id;
      });
      // console.log(d2);
      this.a2 = d2;
      this.n = index;
    },
    all() {
      this.a2 = this.a3;
      this.n = 0;
    },
    des(id) {
      // this.$router.push({path:'/good'})
      this.$router.push("/good?cid="+id)
    }
  },
  created() {
    this.getData();
  }
};
</script>

<style scoped>
.ca{
  width: 5.2rem;
  font-size: 0.16rem;
}
.ca .my-swipe {
  width: 3.4rem;
  height: 1.8rem;
  overflow: hidden;
  margin-top: 0.2rem;
  margin: 0 auto;
}
.ca .my-swipe .van-swipe-item {
  width: 100%;
  text-align: center;
}
.ca .my-swipe .van-swipe-item img {
  width: 3.4rem;
  height: 1.8rem;
  border-radius: 0.1rem;
}
.ca .search {
  width: 4rem;
  height: 0.3rem;
  line-height: 0.3rem;
  text-align: center;
  background: rgb(243, 245, 248);
  border-radius: 0.15rem;
  margin: 0.1rem 0rem 0.1rem 0;
}
.ca input {
  width: 3rem;
  border: cornflowerblue;
  background: rgb(0, 0, 0, 0);
}
.ca .cate {
  display: flex;
}
.ca .cate > div {
  position: fixed;
  left: 1.27rem;
  top: 0.51rem;
  overflow: auto;
  height: 100%;
  width: 3.92rem;
}
.ca ul {
  list-style: none;
  width: 1.1rem;
  border-right: 0.01rem solid #e9e9e9;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0.51rem;
  overflow: auto;
  background: white;
  z-index: 1;
}
.ca ul li {
  width: 1.1rem;
  height: 0.5rem;
  font-size: 0.2rem;
  text-align: center;
  line-height: 0.5rem;
  margin-top: 0.1rem;
}
.ca .fen {
  color: rgb(0, 0, 0);
  border-left: 0.03rem solid rgb(255, 255, 255);
}
.ca .fen.active {
  color: rgb(252, 117, 102);
  border-left: 0.03rem solid rgb(255, 124, 110);
}
.ca ol {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 3.9rem;
  text-align: center;
  padding: 0 0.1rem;
  box-sizing: border-box;
}
.ca ol li {
  width: 30%;
  margin-top: 0.1rem;
}
.ca ol li p {
  font-size: 0.16rem;
  color: #3e4561;
  margin-top: 0.05rem;
}
.ca ol li img {
  width: 1.1rem;
  height: 1.1rem;
  display: block;
}
</style>


