<template>
  <div class="news">
    <van-nav-bar title="专栏详情" left-arrow @click-left="onClickLeft" />
    <h3>{{ article.title }}</h3>
    <img :src="article.pic" alt />
    <div >
        <p v-html="article.content"></p>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      article: []
    };
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    }
  },
  created() {
    console.log("get传值", this.$route.query);
    this.http
      .get(
        "https://api.it120.cc/small4/cms/news/detail?id=" +
          this.$route.query.id
      )
      .then(msg => {
        console.log(msg);
        this.article = msg.data.data;
      });
  }
};
</script>

<style scoped>
.news{
  font-size: 0.16rem;
}
h3 {
  font-size: 0.24rem;
  color: #333333;
  width: 100%;
  text-align: center;
  margin-top: 0.45rem;
  margin-bottom: 0.2rem;
}
.news > img {
  width: 4.85rem;
  height: 3.13rem;
  display: block;
  margin: 0 auto;
}
.news p {
  font-size: 0.2rem;
  color: #333333;
  line-height: 0.48rem;
  text-indent: 2em;
  margin-top: 0.1rem;
}
</style>


