<template>
  <div class="categood">
    <h4>
      <span @click="back">＜</span> 分类商品
    </h4>
    <ul v-if="list.length != 0">
      <li v-for="item of list" :key="item.id" @click="detail(item.id)">
        <img :src="item.pic" alt />
        <p>{{ item.name }}</p>
        <p>￥{{ item.originalPrice }}</p>
      </li>
    </ul>
    <img
      v-else
      class="nuimg"
      src="http://39.105.231.166:7002/static/img/fc9016c81219eb449d20df78381e70af.4b93135.png"
      alt
    />
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: []
    };
  },
  methods: {
    back() {
      this.$router.go(-1);
    },
    detail(id) {
        this.$router.push({ name:'detail', params:{id} })
    }
  },
  created() {
    // alert(this.$route.query.cid)
    this.http.get("https://api.it120.cc/small4/shop/goods/list").then(msg => {
      // console.log(msg)
      if (msg.data.msg == "success") {
        this.list = msg.data.data.filter(item => {
          return item.categoryId == this.$route.query.cid;
        });
        console.log(this.list);
      }
    });
  }
};
</script>

<style scoped>
.categood{
  font-size: 0.16rem;
}
h4 {
  position: fixed;
  left: 0;
  top: 0;
  text-align: center;
  border-bottom: 0.01rem solid rgb(175, 175, 175);
  font-size: 0.2rem;
  width: 5.2rem;
  height: 0.5rem;
  line-height: 0.5rem;
  background: rgb(255, 255, 255);
}
h4 span {
  font-size: 0.25rem;
}
.nuimg {
  margin: 0 auto;
  width: 100%;
  margin-top: 2rem;
}
ul {
  margin-top: 0.7rem;
  display: flex;
  justify-content: space-between;
  padding: 0 0.1rem;
  flex-wrap: wrap;
  list-style: none;
}
ul li {
  width: 2.45rem;
  height: 3.1rem;
  display: flex;
  flex-direction: column;
}
ul li img {
  width: 2.4rem;
  height: 2.4rem;
}
li p {
  left: 0;
  top: 2rem;
  width: 100%;
  height: 0.3rem;
  background: #f0edd4;
  font-size: 0.14rem;
  color: #b69b72;
  text-indent: 0.02rem;
  line-height: 0.3rem;
}
li p:nth-of-type(2) {
  font-size: 0.2rem;
  color: #cf1311;
}
</style>


